.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: white;
    padding: 1rem;
    color: black;
  }
  
  .logo {
    font-size: 1.25rem;
    font-weight: bold;
    margin-right: auto;
  }
  
  .nav-links {
    list-style: none;
    display: flex;
    margin: 0;
    padding: 0;
  }
  
  .nav-links li {
    margin-left: 1.5rem;
  }
  
  .nav-links a {
    color: black;
    text-decoration: none;
    transition: color 0.3s;
  }
  
  .nav-links a:hover {
    color: #ccc;
  }
  
  .social-media {
    display: flex;
    align-items: center;
  }
  
  .social-media a {
    margin-left: 1rem;
    color: black;
    transition: color 0.3s;
  }
  
  .social-media a:hover {
    color: #ccc;
  }
  .main-content {
    display: flex;
    align-items: flex-start;
    margin-top: 2rem;
  }
  
  .profile-picture {
    flex: 0 0 300px;
    margin-right: 2rem;
    margin-left: 10px;
  }
  
  .profile-picture img {
    width: 90%;
    height: auto;
    object-fit: cover;
  }
  
  .profile-picture img:hover {
    color: #ccc; 
  }
  
  .meeting {
    width: 50px;
    height: auto;
    filter: grayscale(100); 
  }
  .content {
    max-width: 500px;
  }

  .content-header {
    position: relative;
    justify-content: space-between;
    align-items: center;
    display: flex;
  }
  
  h1 {
    font-size: 3rem;
    margin-bottom: 1rem;
  }
  
  p {
    font-size: 1.2rem;
  }

  /* Navigation link hover animation */
.nav-links a {
    position: relative;
  }
  
  .nav-links a::after {
    content: '';
    position: absolute;
    bottom: -2px;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: #fff;
    transform: scaleX(0);
    transition: transform 0.3s;
  }
  
  .nav-links a:hover::after {
    transform: scaleX(1);
  }
  
  /* Social media icon hover animation */
  .social-media a {
    transition: transform 0.3s;
  }
  
  .social-media a:hover {
    transform: scale(1.2);
  }
  
  /* Fade-in animation for main content */
  .main-content {
    opacity: 0;
    animation: fadeIn 1s ease-in-out forwards;
  }
  
  @keyframes fadeIn {
    0% {
      opacity: 0;
      transform: translateY(20px);
    }
    100% {
      opacity: 1;
      transform: translateY(0);
    }
  }

  